<template>
  <div class="goods">
    <div>
      <ul class="cl">
        <li>
          <router-link to="/recommend" class="exact">
            <span>热门推荐</span>
          </router-link>
        </li>
        <li>
          <router-link to="/storage">
            <span>存号箱</span>
          </router-link>
        </li>
      </ul>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="van-clearfix"
    >
      <van-cell v-for="item in list" :key="item.pagAwardId">
        <router-link :to="'/gameDetail?id='+item.pagAwardId">
          <img :src="item.icon" class="img-left" />
          <p>
            <span>{{item.name}}</span>
            <span class="gift">
              <span class="count">{{item.amount}}</span> 礼包
            </span>
          </p>
        </router-link>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { List, Cell } from "vant";
import IndexLess from "./index.less";
import axios from "axios";
export default {
  components: {
    [List.name]: List,
    [Cell.name]: Cell
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      page:1
    };
  },

  methods: {
    onLoad() {
      // 异步更新数据
      this.loading = true;
      axios
        .post(process.env.VUE_APP_API + "queryHotRecommendList.do", {
          activityId: 190822080,
          page: this.page++,
          rows: 5
        })
        .then(response => {
          console.log("response", this.list);
          if (this.list.length == 0) {
            this.list = response.data.data.list;
          } else {
            response.data.data.list.forEach((item, index, array) => {
              this.list.push(item);
            });
          }
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          console.log('12',response.data.data.counts);
          if (this.list.length >= response.data.data.counts) {
            this.finished = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>